@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mulish:wght@300&family=Open+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap");
html {
  /*overflow-y: scroll;*/
}

* {
  margin: 0;
  font-family: "Mulish", sans-serif;
}

/* NAVBAR */
.navbar {
  display: flex;
  padding: 1rem;
  justify-content: start;
  align-items: center;
  background-color: #1a1a1a;
  border-bottom: 1px solid #1a1a1a;
}

.navbar h3 {
  color: #fcfcfc;
}

.navbar .links {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar a {
  padding-inline: 1em;
  font-size: large;
  text-decoration: none;
  color: #fafafa;
  transition: all 0.39s ease-in-out;
}

.navbar a:hover {
  color: #42b2f3;
}

/* FOOTER */

.footer {
  display: flex;
  padding: 1rem;
  justify-content: space-between;
  align-items: center;
  background-color: #121212;
}

.social a {
  padding: 0.5rem;
  color: #f2f4f3;
  transition: all 0.39s ease-in-out;
}

.social a:hover {
  color: #42b2f3;
}

.right-section a {
  padding: 1em;
  font-size: larger;
  text-decoration: none;
  color: #fafafa;
  transition: all 0.39s ease-in-out;
}

.right-section a:hover {
  color: #42b2f3;
}

/* CONTACT */

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #040f16;
  height:  100vh;
}

.contact a {
  color: #fafafa;
  padding: 1em;
  text-decoration: none;
  font-size: larger;
}

.contact h1 {
  justify-content: center;
  color: #fafafa;
  padding-bottom: 8vh;
}

.contact h3 {
  color: #fafafa;
}

.contact-phone {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

/* HOME */

.home-header {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50vh;
  padding-inline: 20px;
  background: #0896c2;
}

.home h1 {
  color: #e9f1f7;
}

.home mark {
  color: rgb(43, 222, 139);
  background-color: transparent;
}

.home .mark-name {
  color: rgb(201, 232, 67);
  background-color: transparent;
}

.home .firebase{
  color: orange;
}

.home .webDev {
  color: #58c9eb;
}

.home-main {
  display: flex;
  height: 50vh;
  align-items: center;
  justify-content: center;
  padding-inline: 20px;
  background: #040f16;
}

.home-website-details {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50vh;
  padding-inline: 20px;
  background: #0896c2;
}

.home .h1-link {
  color: rgb(246, 119, 73);
  text-decoration: none;
}

/* PORTOFOLIO */

.portofolio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #040f16;
}

.portofolio h1 {
  color: #fafafa;
  padding: 2em;
  font-size: xxx-large;
}

.portofolio h2 {
  color: #fafafa;
  padding: 1em;
  font-size: xx-large;
}

.resume {
  display: flex;
  height: 20vh;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}

.resume button{
  border-radius: 30px;
  font-size: larger;
  padding: 10px;
  transition: all 0.35s ease-in-out;
}

.resume button:hover{
  border-radius: 30px;
  padding: 15px;
}

/* PORTOFOLIO PROJECT */

.portofolio-projects {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.portofolio-project {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 1em;
  flex: 40%;
  text-align: center;
  border-radius: 15px;
  background-color: #f9fbf2;
  box-shadow: 0 0 0 1px #a8b6e228;
  transition: all 430ms cubic-bezier(0.4, 0, 0.2, 1);
}

.portofolio-project:hover {
  transform: scale(1.05);
  border-radius: 30px;
}

.portofolio-project h2 {
  color: #040f16;
}

.portofolio-projects a {
  font-weight: bold;
  text-decoration: none;
  border-radius: 10px;
  color: #fafafa;
  background-color: #1aa842;
  box-shadow: 0 0 0 1px #1aa842;
  padding: 0.34rem;
  margin-block: 1rem;
}

/* PROJECTS*/

.project-icontitle {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 1em;
}

@media screen and (max-width: 31em) {
  .project-icontitle img {
    display: none;
  }
}

.project-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1em;
  font-size: x-large;
}

.divImage {
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow-x: scroll;
}

.divImage::before,
.divImage::after {
  content: "";
  margin: auto;
}

.imageTitle {
  max-height: 300px;
  margin-inline: 0.4em;
}

.project-data {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
}

.project-features {
  margin-top: 2rem;
  text-align: center;
  line-height: 3rem;
  font-size: larger;
  font-weight: 500;
  font-family: "Roboto", sans-serif;
  box-shadow: 0px -5px 4px -5px #131313;
}

.project-features p {
  font-weight: bold;
}

.project-desc {
  display: flex;
  flex-wrap: wrap;
  margin-inline: 1em;
  margin-block: 3em;
  justify-content: center;
  line-height: 1.5rem;
  box-shadow: 0 -5px 4px -5px #131313;
}

.project-desc h2 {
  margin-top: 2em;
  margin-bottom: 1em;
}

.project-desc p {
  margin-block: 1em;
  margin-inline: 1em;
  font-size: medium;
  word-spacing: 0.19rem;
  line-height: 1.7rem;
  font-weight: 400;
  font-family: "Roboto", sans-serif;
}

.downloadLink {
  display: flex;
  justify-content: center;
  flex: 1;
  margin-block-end: 2em;
}

.downloadLink img {
  width: 19rem;
}
